import React from "react";
import PropTypes from 'prop-types';
import './Movie.css';
import { Link } from 'react-router-dom';

function Movie({title, year, summary, poster, genres}) {
    let shortSummary = summary.slice(0, 180) + '...';

    return <div className="movie">
            <Link to="/movie-detail"state={{title, year, summary, poster, genres}}>
             <img src={poster} alt={title} title={title}/>
             <div className="movie__data">
              <h3 className="movie__title">{title}</h3>
              <h5 className="movie__year">{year}</h5>
              <ul className="movie__genres">
                {genres.map((genre, index) => {return <li key={index} className="movie_genres">{genre}</li>;})}
              </ul>
              <p className="movie__summary">{shortSummary}</p>
             </div>
            </Link>
            </div>
}

Movie.propTypes = {year:PropTypes.number.isRequired,
                   title:PropTypes.string.isRequired,
                   summary:PropTypes.string.isRequired,
                   poster:PropTypes.string.isRequired,
                   genres:PropTypes.arrayOf(PropTypes.string).isRequired};

export default Movie;